import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Navigate ,  BrowserRouter ,Route , Routes ,Link, useNavigate } from 'react-router-dom';

function StaticExample({setData ,data}) {
    const [flag , setFlag] = useState(false); 
    const [login , setLogin] = useState("");
    const correct = "admin123"
    const navigate = useNavigate();

    const [newItem,setNewItem] = useState({
        id : "",
        title : "",
        location : "",
        method : "",
        content : "",
        impPath : "",
        youTube : ""
    })

    const handle = (e)=>{ 
        setLogin(e.target.value);
        console.log(login);
    }
    function check(){
 
        if(login == correct){
            alert("");
            setFlag(true);
            console.log(flag);

        }else{
            alert("");
            setFlag(false);
        }
    }

    const input= (e)=>{
        setNewItem({
                ...newItem,
                [e.target.name]: e.target.value // []     key  
            })
        console.log(newItem);
    }


  return (
    <div>
        {
            flag === false ? 
             <div
             className="modal show"
             style={{ display: 'block', position: 'initial' }}
           >
             <Modal.Dialog>
               <Modal.Header closeButton>
                 <Modal.Title></Modal.Title>
               </Modal.Header>
       
               <Modal.Body>
                 <input type='text' value ={login} onChange={handle}/>
               </Modal.Body>
       
               <Modal.Footer>
                 <Button variant="primary" onClick={()=>{
                   check();
                   
                 }} > </Button>
               </Modal.Footer>
             </Modal.Dialog>
           </div>
           : <div>
                <input type='id' name = "id" placeholder='id +1' onChange={input}></input> <br></br>
                <input type='text' name = "title" placeholder='' onChange={input}></input> <br></br>
                <input type='text' name = "location" placeholder='' onChange={input} ></input>  <br></br>
                <input type='text' name = "method" placeholder='' onChange={input} ></input>  <br></br>
                <input type='text' name = "content" placeholder='' onChange={input}></input>  <br></br>
                <input type='text' name = "imgPath"  placeholder='' onChange={input}></input>  <br></br>
                <input type='text' name = "youTube"  placeholder='' onChange={input}></input>  <br></br>
                <button onClick={()=>{
                    console.log("");
                    setData([newItem,...data]);
                    console.log("gkgkgk");
                    navigate("/");
                    console.log(data);
                    
                }}></button>
               

            </div>

        }
   

    </div>
 


  );
}

export default StaticExample;